:root {
  --font-jost: "Jost", sans-serif;
}

:root {
  --gold: hsl(26, 35%, 60%);
  --gray: hsl(240, 1%, 23%);
  --black: hsl(0, 0%, 8%);
  --blueish: hsl(240, 18%, 17%);
  --darkblue: hsl(240, 33%, 9%);
  --blackish: hsl(240, 33%, 9%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-jost);
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.card {
  font-size: 1.25rem;
}

.toggle__button {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
}

.topbar {
  width: 100%;
  background-color: var(--black);
  height: 2.5rem;
}

.header {
  height: 7.5rem;
  width: 100%;
  margin-top: 1rem;
}
.header__logo img {
  height: 7.5rem;
}
.header__links {
  flex: 0.2;
  justify-content: space-evenly;
  list-style: none;
  letter-spacing: 0.125rem;
  font-weight: 500;
}
.header a {
  color: var(--blackish);
}